'use client'

import { useEffect, useState } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react'; // Cambiar importación
import SwiperCore, { Navigation, Autoplay } from 'swiper'; // Usamos SwiperCore con los módulos
import 'swiper/swiper-bundle.css'; // Asegúrate de importar los estilos de Swiper
import { fetchFeaturedMovies, fetchGenres, fetchMoviesByGenre } from '@/lib/tmdb'; // Asegúrate de tener estas funciones implementadas
import Link from 'next/link';

export default function MainDashboard() {
  const [featuredMovies, setFeaturedMovies] = useState([]);
  const [filterMovies, setFilterMovies] = useState({});
  const [genreFilters, setGenreFilters] = useState([]);

  // Cargar las películas y géneros
  useEffect(() => {
    const loadMovies = async () => {
      const movies = await fetchFeaturedMovies();
      setFeaturedMovies(movies);

      const genres = await fetchGenres();
      setGenreFilters(genres);

      // Cargar las películas por género
      const moviesByGenre = {};
      for (const genre of genres) {
        const moviesForGenre = await fetchMoviesByGenre(genre.id);
        moviesByGenre[genre.name] = moviesForGenre;
      }
      setFilterMovies(moviesByGenre);
    };

    loadMovies();
  }, []);

  return (
    <div className="p-8 text-white bg-black">
      {/* Carrusel de fondo de películas */}
      <div className="relative">
        <Swiper
          spaceBetween={20}
          slidesPerView={3}
          autoplay={{ delay: 5000 }}
          navigation={true} // Habilitar navegación
          modules={[Navigation, Autoplay]} // Incluir los módulos necesarios
          className="h-full"
        >
          {featuredMovies.map((movie) => (
            <SwiperSlide key={movie.id}>
              <Link href={`/details/movie/${movie.id}`}>
                <div className="relative cursor-pointer">
                  <img
                    src={`https://image.tmdb.org/t/p/original${movie.backdrop_path}`}
                    alt={movie.title}
                    className="w-full h-full object-cover rounded-lg"
                  />
                  <div className="absolute bottom-4 left-4 text-white">
                    <h3 className="text-2xl font-bold">{movie.title}</h3>
                  </div>
                </div>
              </Link>
            </SwiperSlide>
          ))}
        </Swiper>
      </div>

      {/* Carruseles por filtros */}
      <div className="mt-12">
        <div className="space-y-12">
          {genreFilters.map((genre) => (
            <div key={genre.id}>
              <h3 className="text-xl font-semibold">{genre.name}</h3>
              <Swiper
                spaceBetween={20}
                slidesPerView={10} // Por defecto se muestran 10 elementos
                navigation={true} // Habilitar navegación
                modules={[Navigation]} // Incluir el módulo de navegación
                className="mt-6"
                breakpoints={{
                  640: {
                    slidesPerView: 2, // En pantallas pequeñas, mostrar 2 películas
                    spaceBetween: 10,
                  },
                  768: {
                    slidesPerView: 4, // En pantallas medianas, mostrar 4 películas
                    spaceBetween: 10,
                  },
                  1024: {
                    slidesPerView: 10, // En pantallas grandes, mostrar 10 películas
                    spaceBetween: 20,
                  },
                }}
              >
                {filterMovies[genre.name]?.map((movie) => (
                  <SwiperSlide key={movie.id}>
                    <Link href={`/details/movie/${movie.id}`}>
                      <div className="cursor-pointer">
                        <img
                          src={`https://image.tmdb.org/t/p/w300${movie.poster_path}`}
                          alt={movie.title}
                          className="w-full h-full object-cover rounded-lg"
                        />
                        <div className="mt-2 text-center">
                          <p className="font-semibold">{movie.title}</p>
                          <p className="text-sm">{movie.vote_average} ⭐</p>
                        </div>
                      </div>
                    </Link>
                  </SwiperSlide>
                ))}
              </Swiper>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}
